module.exports = {
  "theme": "dark",
  "on": {
    "init": function(event) {
      var target = event.target;
      //   var resDic = target.getResourceDic;

      var DIC = target.getRootScope()['myDicts'];

      //-------------------------------------------------------


      function getTableData1(params, pageRequest, cb) {

        target.postService("alertQueryFlexService", "getAlertByPage", [params, pageRequest], function(tc) {

          var alerts = tc.data.data;
          cb(tc.data.data, tc.data.total)
        });
      }

      //-------------------------------------------------------
      //
      var render = function(datas, e) {




        var param = {
          "domain": "/0/515445641576227/515445641576264/515445641576272/554925002946064/",
          "severities": "1,2,3,4",
          "states": "0,5,-100",
        };

        target.render({
          data: datas,
          params: param,
          paging: getTableData1,
          format: [{
            "name": "",
            "value": "appName",
            "type": "text",
            //"width" : 300
            "format": function(elem) {
              if (elem == 1) {
                return "";
              } else if (elem == 2) {
                return "";
              } else if (elem == 3) {
                return "";
              } else {
                return "";
              }
            }
          }, {
            "name": "",
            "value": "message",
            "type": "text",
          }, {
            "name": "",
            "value": "devName",
            "type": "text",
          }, {
            "name": "",
            "type": "text",
            "value": "state",
            "format": function(elem) {
              if (elem == -100) {
                return "";
              } else if (elem == 0) {
                return "";
              } else if (elem == 5) {
                return "";
              } else {
                return "";
              }
            }
          }, {
            "name": "",
            "type": "priority",
            "value": "severity",
            "format": function(elem) {
              if (elem == 2) {
                return "";
              } else if (elem == 3) {
                return "";
              } else if (elem == 4) {
                return "";
              } else {
                return "";
              }
            }
          }, {
            "name": "",
            "value": "firstArisingTime",
            "type": "date"
          }, {
            "name": "",
            "value": "count"
          }, {
            "name": "",
            "value": "arisingTime",
            "type": "date"
          }, {
            "name": "",
            "type": "buttonGroup",
            "content": [{
              type: "button",
              label: "",
              icon: "glyphicon glyphicon-cog",
              btnclass: "btn btn-primary",
              on: {
                click: function(elem) {
                  event.target.postService("ticketTaskService", "getDealTicketListByPage", [{
                      "attrs": {
                        "origId": [elem.row.alertId]
                      },
                      "categorys": "290,280"
                    },
                    {
                      "start": 0,
                      "length": 10
                    }
                  ], function(dt) {
                    if (dt.data.data.length > 0) {
                      target.navigateToTracker(dt.data.data[0].ticketNo);
                    } else {
                      target.growl("", "warning")
                    }
                  })
                }
              }
            }]
          }]
        });
      };

      render([]);


    }
  }
}